import React, { Component } from 'react';

class CharInfo extends Component {
     constructor(props) {
        super(props);

        this.state = {};

        this.rolled = false;
        this.level = 1;
        this.class = null;
        this.generation = null;
    
        this.handleLevel = this.handleLevel.bind(this);
        this.handleClass = this.handleClass.bind(this);
        this.handleGenerationMethod = this.handleGenerationMethod.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    

    handleLevel(event) {
        this.level = event.target.value;
        this.forceUpdate()
    }

    handleClass(event) {
        if (event.target.value === "Class"){
            this.class = null;
        } else {
            this.class = event.target.value;
        }
        this.forceUpdate();
    }

    handleGenerationMethod(event) {
        if (event.target.value === "Generation Method"){
            this.generation = null;
        } else{
            this.generation = event.target.value;    
        }
        this.forceUpdate();
    }
    
    handleSubmit(event) {
        if (this.class === null || this.class === null){
            console.error("Please choose class and generation method before you proceed")
        } else {
        this.rolled = true;
        this.props.onClick({rolled: this.rolled,
                            level: this.level,
                            class: this.class,
                            generation: this.generation});
        }
        event.preventDefault();
    }
    
      render() {
          var button = '';
        if (this.class !== null && this.generation !== null){
            button = 'button-active'
        } else {
            button = 'button-inactive'
        }
        const cls_name = this.props.isMobile ? "mobile-input-box" : "input-box"
        return (
          <div className={cls_name}>
            <form className="character-form" onSubmit={this.handleSubmit}>
                <label className="lb-label" for="lb" >Level:</label> 
                <input className="levelbox" type="number" min="1" max="20" value={this.level} onChange={this.handleLevel} id="lb"/>
                <select className="dropdown-class" onChange={this.handleClass}>
                    <option>Class</option>
                    <option>Artificer</option>
                    <option>Blood Hunter</option>
                    <option>Barbarian</option>
                    <option>Bard</option>
                    <option>Cleric</option>
                    <option>Druid</option>
                    <option>Fighter</option>
                    <option>Monk</option>
                    <option>Paladin</option>
                    <option>Ranger</option>
                    <option>Rogue</option>
                    <option>Warlock</option>
                    <option>Wizard</option>
                </select>
                <select className="dropdown-generation-method" onChange={this.handleGenerationMethod}>
                    <option>Generation Method</option>
                    <option>3d6</option>
                    <option>4d6</option>
                    <option>CR</option>
                </select>
                <input className={button} type="submit" value="Roll" />
            </form>
          </div>
        );
    }
}


export default CharInfo;