import React, { Component } from 'react';

class Rolls extends Component {
    constructor(props) {
        super(props);
        this.keys = ["STR", "DEX", "CON", "INT", "WIS", "CHA", "HEALTH"]

        this.rolls = ["--", "--", "--", "--", "--", "--"];
        this.health = "--";
    }
    render() {
        console.log(this.props.rolled)
        if (this.props.rolled) {
            this.rolls = this.props.rolls;
            this.health = this.props.health;
        }
        console.log(this.rolls)
        const ability_rolls = []
        for (var i = 0; i<6; i++){
            ability_rolls.push(
                <div className="stat-block">
                    <div className="stat">
                         {this.rolls[i]}
                    </div>
                    <div className={"lab-" + this.keys[i].toLowerCase()}>
                         {this.keys[i]}
                    </div>
                </div>
            );
        }   

        ability_rolls.push(
            <div className="stat-block" title="Health EXCLUDING CON modifier">
                <div className="stat">
                     {this.health}
                </div>
                <div className="lab-health">
                     HP
                </div>
            </div>
        );
        const cls_name = this.props.isMobile ? "stats-mobile" : "stats"
        return (
            <div className={cls_name}>
                {ability_rolls}
            </div>
        );
    }
}

export default Rolls;